.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0); }

html {
  font-family: "PTMono-Regular", "Courier New", monospace;
  font-size: 16px;
  color: #666666; }

body {
  background-color: #E5E5E5; }

h1, h2, h3, h4, h5, p {
  margin: 0; }

a {
  text-decoration: none; }

@font-face {
